.view-tool-container {
  float: left;
  display: flex;

  .tool-separator {
    float: left;
    height: 14px;
    margin: 6px 2px;
    width: 0px;
    border-left: 1px solid var(--gray9);
  }

  .tool-btn {
    float: left;
    width: 18px;
    height: 18px;
    padding: 4px;
    margin: 0 2px;

    .svg-icon {
      width: 100%;
      height: 100%;
      display: inline-block;
      background-repeat: no-repeat;
      background-size: 94%;
      filter: invert(70%);
    }

    .no-hover-icon {
      filter: invert(30%);
    }

    &:hover .svg-icon {
      filter: invert(30%);
      cursor: pointer;
    }

    &:active {
      background: #bcbcbc;
      cursor: pointer;
    }

    &[disabled] {
      pointer-events: none;
    }
  }

  .selected-tool {
    filter: invert(0%) !important;
  }

  .narrow-tool-btn {
    padding: 4px 0;
  }

  .arrow-icon {
    background-image: url("data:image/svg+xml;base64,");
  }

  .lasso-icon {
    background-image: url("data:image/svg+xml;base64,");
  }

  .selection-suggestion-icon {
    background-image: url("data:image/svg+xml;base64,");
  }

  .selection-non-suggestion-icon {
    background-image: url("data:image/svg+xml;base64,");
  }

  .rect-icon {
    background-image: url("data:image/svg+xml;base64,");
  }

  .table-icon {
