.view {
  .zoom-panel {
    position: absolute;
    bottom: 30px;
    right: 30px;
    padding: 10px;
    font-size: 14px;
    color: var(--gray15);
    background: var(--gray1);
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
    border-radius: 18px;
    text-align: center;
  }

  .player-widget {
    position: absolute;
    width: calc(98% - 70px);
    bottom: 38px;
    left: 80px;
    height: var(--title-height);
  }

  $number-btn: 10;
  $btn-space: 60px;

  @function bottom-offset($number) {
    @return (-($number-btn - 1) / 2.0 + $number) * $btn-space - 35px;
  }

  .multiselect-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(0)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .preview-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(1)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .confirm-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(2)} + 62.5%);
    fill: darkseagreen;
    pointer-events: none;
    opacity: 0.3;
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .edit-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(3)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .reload-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(4)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .revert-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(5)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .export-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(6)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .save-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(7)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }

  .open-btn-container {
    position: fixed;
    bottom: calc(#{bottom-offset(8)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }
  .selection-mode-btn-container{
    position: fixed;
    bottom: calc(#{bottom-offset(9)} + 62.5%);
    left: 4px;
    width: 70px;
    height: 70px;
  }
  .btn-panel {
    position: fixed;
    bottom: calc(#{- ($number-btn - 1) / 2.0 * $btn-space - 27px} + 62.5%);
    left: 0;
    width: 70px;
    height: ($number-btn - 1) * $btn-space + 70px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: white;
    box-shadow: 4px 4px 15px #bbb;
  }

  .dis {
    opacity: 0.3;
    pointer-events: none;
  }

  .draw-board {
    position: absolute;
    right: 0px;
    bottom: 0px;
    width: calc(100% - 90px);
    height: 300px;
    background-color: white;
    // border: 2px solid #EEE;
    opacity: 0.8;
    display: none;
  }
}

.data-view-content {
  background: none;
  display: flex;
  flex-direction: column;
}

.attr-wrapper {
  margin-top: 6px;
  box-sizing: border-box;
  padding: 0px 10px;
  width: 100%;
  display: flex;

  & div {
    clear: both;
    float: left;

    &:first-child {
      flex: 3;
    }

    &:last-child {
      flex: 2;
      transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    }

    div {
      width: 100%;
    }
  }
}

.non-sortable-attr-wrapper {
  &>div {
    &:last-child {
      flex: 0;
      width: 0;
      opacity: 0;
    }
  }

  #attrBtnContainer span {
    width: 100%;
  }
}

.data-column-title {
  float: left;
  text-transform: capitalize;
  font-size: 10pt;
  display: block;
  box-sizing: border-box;
  padding: 0 4px;
  width: 100%;
  height: 26px;
  line-height: 26px;
  background: var(--gray7);
  color: var(--gray14);
}

.data-table-wrapper {
  box-sizing: border-box;
  // padding: 0px 10px;
  // margin-top: 10px;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;

  .data-table-title {
    box-sizing: border-box;
    padding: 0 4px;
    height: 26px;
    line-height: 26px;
    background: var(--gray7);

    .data-table-title-text {
      float: left;
      text-transform: capitalize;
      color: var(--gray14);
      font-size: 10pt;
    }

    .drop-down {
      float: right;
      width: 14px;
      height: 14px;
      margin: 6px;
      background-color: none;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: 100% auto;
      transform: rotate(180deg);
      filter: invert(50%);

      &:hover {
        filter: invert(0%);
        cursor: pointer;
      }
    }

    .left-up-icon {
      transform: rotate(0deg);
    }
  }

  .data-table-container {
    width: 100%;
    flex: 1;
    overflow: auto;
    // margin-top: 6px;
    transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  }

  .hidden-data-table {
    flex: none;
    height: 0px;
    overflow: hidden;
  }
}

